import React from 'react';
import connect from '@vkontakte/vkui-connect-promise';
import {View} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import {Users} from './api';

import Home from './panels/Home';
import VueFriends from './panels/VueFriends';

class App extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            activePanel: 'home',
            fetchedUser: null,
            access_token: null
        };
    }

    componentDidMount () {
        connect.send('VKWebAppGetAuthToken', {app_id: 6940370, scope: 'friends,photos,video,pages,status,notes,wall,docs,groups,stats,market'})
            .then(res => {
                this.setState({access_token: res.data.access_token});
                console.info('access_token', res);
                this.getUser();
            })
            .catch(e => console.error('ERR VKWebAppGetUserInfo', e));
        // connect.send("VKWebAppGetUserInfo", {})
        //     .then(res => this.setState({fetchedUser: res.data}))
        //     .catch(e => console.error('ERR VKWebAppGetUserInfo', e));
    }

    getUser () {
        Users.getUsers({
            fields: 'photo_200, contacts, site, education, universities, status, common_count, occupation, nickname, relatives, relation, personal, connections',
            access_token: this.state.access_token
        })
            .then(res => this.setState({fetchedUser: res.response[0]}))
            .catch(e => console.log(e));
    }

    go = (e) => {
        this.setState({activePanel: e.currentTarget.dataset.to})
    };

    render () {
        return (
            <View activePanel={this.state.activePanel}>
                <Home id="home" fetchedUser={this.state.fetchedUser} go={this.go}/>
                <VueFriends
                    id="friends"
                    go={this.go}
                    access_token={this.state.access_token}
                />
            </View>
        );
    }
}

export default App;
