import {
    Panel,
    Button,
    Group,
    Div,
    Avatar,
    PanelHeader,
    HeaderButton,
    Cell,
    IOS,
    Spinner,
    Progress
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import {Friends, Users} from '../api';

class VueFriends extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            usersInfo: [],
            isSpinner: true,
            countFriendsInstagram: 0
        };
    }

    getFriends () {
        let offsetFriendsInterval = 0;
        this.friendsInterval = setInterval(() => {
            Friends.getFriends({
                count: 100,
                offset: offsetFriendsInterval,
                access_token: this.props.access_token
            })
                .then(res => {
                    offsetFriendsInterval += 100;
                    if (res.data.error) {
                        clearInterval(this.friendsInterval);
                        this.setState({isSpinner: false});
                    } else {
                        this.getUsersInfo(res.data.response.items)
                            .then(res => {
                                this.setState({isSpinner: false});
                                if (res.error) {
                                    console.error('Ошибка', res.error);
                                } else if (!res.response.length) {
                                    clearInterval(this.friendsInterval);
                                } else {

                                    this.setState(state => state.usersInfo.push(...res.data.response));
                                    this.forceUpdate();
                                }
                            })
                            .catch(e => console.log('ERR getUsersInfo', e));
                    }
                })
                .catch(e => console.log('ERR getFriends', e));
        }, 2000);
    }

    getUsersInfo (ids) {
        const strFriendsIds = ids.slice(0, 100).join(',');
        return Users.getUsers({
            user_ids: strFriendsIds,
            fields: 'photo_200, contacts, site, education, universities, status, common_count, occupation, nickname, relatives, relation, personal, connections',
            access_token: this.props.access_token
        });
    }

    goInstagram (id) {
        return window.open(`https://www.instagram.com/${id}`);
    }

    prev (e) {
        clearInterval(this.subscribersInterval);
        clearInterval(this.friendsInterval);
        this.props.go(e);
    }

    componentDidMount () {
        this.getFriends();
    }

    render () {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.prev.bind(this)} data-to="home">
                        {this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Друзья
                </PanelHeader>
                <Group title={`Друзья в инстаграме ${this.state.usersInfo.length}`}>
                    {
                        this.state.isSpinner &&
                        <Cell size="l">
                            <Spinner size="large" style={{margin: '150px 0'}}/>
                        </Cell>
                    }
                    {
                        this.state.usersInfo.map((friend, index) => {
                            return (
                                <div key={index}>
                                    {
                                        friend.instagram &&
                                        <Cell
                                            size="l"
                                            description={friend.status || ''}
                                            before={<Avatar src={friend.photo_200_orig}/>}
                                            bottomContent={
                                                <Button onClick={
                                                    this.goInstagram.bind(this, friend.instagram)
                                                }>Instagram</Button>
                                            }
                                        >
                                            {`${friend.first_name} ${friend.last_name}`}
                                        </Cell>
                                    }
                                </div>
                            );
                        })
                    }
                </Group>
            </Panel>
        );
    }
}

VueFriends.propTypes = {
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired
};

export default VueFriends;
